import React, { PureComponent } from 'react'
import InputAdornment from '@material-ui/core/InputAdornment'
import Form, { IProps } from './Form'
import FormGenerator from './FormGenerator'
import { PROPERTY } from '../../graphql/query/remote'
import { CREATE_PROPERTY, UPDATE_PROPERTY } from '../../graphql/mutation/remote'
import { getDate } from '../../utils/date'

export const defaults: IFormGenerator = {
  name: {
    type: 'text',
    default: '',
    required: true,
  },
  location: {
    type: 'text',
    default: '',
    required: true,
  },
  price: {
    type: 'number',
    default: 0,
    required: true,
    validate: (val: number) => val < 0.01 ? 'Must be greater then zero' : null,
    startAdornment: <InputAdornment position='start'>₹</InputAdornment>,
  },
  buyer: {
    type: 'text',
    default: '',
    required: true,
    validate: (val: string) => {
      return val.length < 1 ? 'required' : null
    },
  },
  buyerNumber: {
    type: 'number',
    default: 0,
    max: 9999999999,
    required: true,
    startAdornment: <InputAdornment position='start'>+91</InputAdornment>,
    validate: (val: number) => {
      return String(val).length !== 10 ? 'Invalid phone number' : null
    },
  },
  totalReceivedAmount: {
    type: 'number',
    default: 0,
    required: true,
    min: 0,
    startAdornment: <InputAdornment position='start'>₹</InputAdornment>,
  },
  nextDueDate: {
    type: 'date',
    default: getDate(),
    required: true,
    validate: (val: string, vals: any) => {
      if (vals.totalReceivedAmount === vals.price) {
        return null
      }
      if (new Date(val).getTime() <= new Date(getDate()).getTime()) {
        return 'Must be a future date'
      }
      return null
    },
  },
  note: {
    type: 'text',
    default: '',
  },
}

class CreateProperty extends PureComponent<IProps> {
  render() {
    const { id } = this.props.match.params
    return (
      <Form
        {...this.props}
        query={PROPERTY}
        defaults={defaults}
        skipOnUpdate={['price', 'totalReceivedAmount']}
      >
        {({ update, defaults, queryResult }: IFormProps) => {
          return (
            <FormGenerator
              {...this.props}
              defaults={defaults}
              mutation={update ? UPDATE_PROPERTY : CREATE_PROPERTY}
              title={!update ? 'Create a new property' : 'Update this property'}
              submitText={!update ? 'Create Property' : 'Update Property'}
              mutateFunc={(fn, data) => fn({ variables: { id, data } })}
            >
            </FormGenerator>
          )
        }}
      </Form>
    )
  }
}

export default CreateProperty
